<template>
  <div v-if="show" class="modal overlay" :class="classes" @click="onClickOverlay" ref="overlay">
      <div class="modal__content" :style="style">
          <slot></slot>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Modal',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    closeOverlay: {
      type: Boolean,
      default: true
    },
    width: {
      type: [String, Number],
      default: 'auto'
    },
    classes: {
      type: String,
      default: ''
    }
  },
  data(){
    return {
      style: {
        width: this.width === 'auto' ? undefined : parseFloat(this.width) + 'px'
      }
    }
  },
  methods: {
    onClickOverlay(e){
      if(this.closeOverlay && e && e.target == this.$refs.overlay){
        this.$emit('update:show', false);
        this.$emit('modal-close', e);
      }
    }
  }
}
</script>

<style>
  .modal.overlay{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .modal__content {
    border: 1px solid #dddddd;
  }
</style>
