import * as userService from '../../services/user.service.js'

export default {
  namespaced: true,
  state: {
    user: {}
  },
  getters: {
    isUserExist(state){
      return !!state.user.email
    }
  },
  mutations: {
    setUser(state, user){
      state.user = user
    },
    removeUser(state, user){
      state.user = {}
    }
  },
  actions: {
    getMe({commit, dispatch}, payload){
      return new Promise((resolve, reject) => {
        userService.getMe()
          .then(response => {
            commit('setUser', response.data)
            resolve(response)
          })
          .catch(error => {
            commit('removeUser', error.data)
            reject(error)
          })
      })
    }
  }
}
