<template>
    <div class="tree__node draggable droppable" >
          <div class="tree__node-item">
            <div class="tree__node-item-lside"></div>
            <div class="tree__node-item-rside">
                <div class="tree__node-item-toggle"  @click="toggle">
                    <template v-if="hasNodes">
                      <font-awesome-icon  v-if="node.isOpened" icon="caret-down" />
                      <font-awesome-icon  v-else icon="caret-right" />
                    </template>
                </div>
                <div class="tree__node-item-name">
                  {{ node.name }}
                </div>
                <div class="tree__node-item-actions" >
                  <font-awesome-icon icon="plus"
                                     :title="addTitle"
                                     @click.stop="onAddNode"
                  />
                  <font-awesome-icon icon="pen"
                                     :title="editTitle"
                                     @click.stop="onEditNode"
                  />
                  <font-awesome-icon icon="times"
                                     :title="deleteTitle"
                                     @click.stop="onDeleteNode"
                  />
                </div>
            </div>
          </div>
          <div class="tree">
            <TreeNode v-show="node.isOpened"
                      v-if="node.nodes"
                      v-for="currNode in node.nodes"
                      :key="currNode.name"
                      :node="currNode"
                      :bus="bus"
                      :addTitle="addTitle"
                      :editTitle="editTitle"
                      :deleteTitle="deleteTitle"
            ></TreeNode>
          </div>
    </div>
</template>

<script>


export default {
  name: 'TreeNode',
  props: ['node', 'bus', 'addTitle', 'editTitle', 'deleteTitle'],
  methods: {
    toggle(e){
      if(this.hasNodes){
        this.node.isOpened = !this.node.isOpened;
      }

      if(this.node.isOpened){
        this.bus.$emit('toogle-opened', e);
      }else{
        this.bus.$emit('toogle-closed', e);
      }
    },
    onEditNode(e){
      this.bus.$emit('edit-node', this.node);
    },
    onAddNode(e){
      this.bus.$emit('add-node', this.node);
    },
    onDeleteNode(e){
      this.bus.$emit('delete-node', this.node);
    }
  },
  computed: {
    hasNodes(){
      if(this.node.nodes && this.node.nodes.length){
        return true
      }
      else{
        return false
      }
    }
  }
}

</script>

