<template>
    <header class="header">
        <div class="header__top">
            <div class="container header__top-container">
                <div class="header__logo">
                  Make stores
                </div>
                <div class="header__nav">
                    <AppNav :items="navItems"
                            :route-path="routePath"
                    ></AppNav>
                </div>
                <div class="header__profile">
                    <div class="dropdown-menu">
                      <a class="dropdown-menu__title" href="#">Профиль</a>
                        <div class="dropdown-menu__content">
                            <div class="menu">
                              <a href="#" class="menu__item">Тарифы</a>
                              <a href="#" @click="signOut" class="menu__item">Выйти</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="activeNavItemChildren.length" class="header__bottom">
            <div class="container header__bottom-container">
                <div class="header__nav">
                    <AppNav :items="activeNavItemChildren"
                            :route-path="routePath"
                    ></AppNav>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
import AppNav from '../../components/Nav.vue'

export default {
  methods: {
    setActiveNavItem(index){
      this.$store.dispatch('nav/setActiveNavItem', index);
    },
    signOut(){
      this.$store.dispatch('auth/signOut');
      this.$router.push({ name:'signIn'})
    }
  },
  computed: {
      routePath(){
        return this.$route.path;
      },
      navItems(){
        return this.$store.getters['nav/items'];
      },
      activeNavItemChildren(){
        return this.$store.getters['nav/activeNavItemChildren'];
      }
  },
  watch: {
    routePath: {
      handler: function () {
        for(let i = 0; i < this.navItems.length; i++){
          if( this.routePath.indexOf(this.navItems[i].url ) != -1){
            this.setActiveNavItem(i);
          }
        }
      },
      immediate: true
    }
  },
  components: {
    AppNav
  }
}
</script>

