  .header {
    font-size: 16px;

    background-color: #ffffff;
  }
  .header__top,
  .header__bottom{
    box-shadow: inset 0 -2px 0 #ebedf5;
  }
  .header__top-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
