import axios from 'axios'
import {AUTH_URL} from '../env.js'


export function getTokens({email, password}){
  return new Promise((resolve, reject) => {
    axios.post(`${AUTH_URL}/token`, {email, password})
      .then(response => {
        return resolve(response)
      })
      .catch(error => {
        return reject(error);
      })
  });
}

export function refreshTokens(refreshToken) {
  return new Promise((resolve, reject) => {
    axios.post(`${AUTH_URL}/token/refresh`, {refresh: refreshToken})
      .then(response => {
        return resolve(response)
      })
      .catch(error => {
        return reject(error)
      })
  })
}

export function signUp({email, password, repeatPassword, domainName}){
  return new Promise((resolve, reject) => {
    axios.post(`${AUTH_URL}/signup/`, {
      email,
      password,
      repeatPassword,
      domainName
    })
      .then(response => {
        return resolve(response)
      })
      .catch(error => reject(error))

  });
}
