import Vue from 'vue'
import App from './App.vue'

import { store } from './store'
import { router } from './router'
import axios  from 'axios'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faEnvelope, faKey, faStore, faCaretRight, faCaretDown, faPlus, faPen, faTimes} from '@fortawesome/free-solid-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faEnvelope, faKey, faInstagram, faStore, faCaretRight, faCaretDown, faPlus, faPen, faTimes)

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.config.productionTip = false

import WordCase from './utils/word-case.js'
import './registerServiceWorker'

axios.interceptors.request.use(function (request) {
  request.data && WordCase.objCamelKeysToSnakeCase(request.data)

  if(store.getters['auth/isAuth']){
    request.headers = {
      Authorization: 'Bearer ' + store.getters['auth/token'].access
    }

    if (store.getters['auth/isAccessTokenExpired']
        && store.getters['auth/token'].refresh
        && request.url.indexOf('token/refresh') == -1){

      return store.dispatch('auth/refreshTokens')
              .then(response => {
                request.headers = {
                  Authorization: 'Bearer ' + store.getters['auth/token'].access
                }
                return request;
              })
              .catch(error => reject(error))
    }else{
      return request
    }

  }
  else{
    return request
  }
}, function (error) {
  console.log('error')
  error.response && WordCase.objSnakeKeysToCamelCase(error.response.data)
  return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {
  response.data && WordCase.objSnakeKeysToCamelCase(response.data);
  return response;
}, function (error) {
  error.response && WordCase.objSnakeKeysToCamelCase(error.response.data);
  return Promise.reject(error);
});


new Vue({
  store,
  router,
  render: h => h(App),
}).$mount('#app')
