import axios from 'axios'
import {API_URL} from '../env.js'

import {store} from '../store'


export function getMe(){
  return new Promise((resolve, reject) => {
    axios.get(`${API_URL}/users/me`)
      .then(response => {
        resolve(response)
      })
      .catch(error => {
        reject(error)
      })
  });
}
