<template>
  <div class="tree-categories">
      <tree v-model="nodes"
            addTitle="Добавить подкатегорию"
            editTitle="Редактировать категорию"
            deleteTitle="Удалить категорию"
            @edit-node="onEditNode"
            @add-node="onAddNode"
            @delete-node="onDeleteNode"
            @move-node="onMoveNode"
      ></tree>
      <br>
      <Btn v-if="nodes.length"
           hot block large
           title="Добавить категорию"
           @click="onAddNode({})"
      >
        Добавить
      </Btn>



      <ModalDefault :show.sync="defaultModal.show"
                    @modal-close="onCloseModal"
      >
        <template v-slot:ModalHeadTitle>
          {{ nodeEditing.name || 'Новая категория' }}
        </template>

        <template v-slot:ModalClose>
          <font-awesome-icon @click="defaultModal.show=false;" icon="times"></font-awesome-icon>
        </template>

        <template v-slot:ModalBody>
          <Card>
            <div class="form">
                <div class="field">
                  <label>Название категории</label>
                  <input type="text" v-model="nodeEditing.name" />
                </div>

                <div class="field">
                  <label>Видимость в навигации</label>
                  <input type="checkbox" v-model="nodeEditing.isVisible" />
                </div>

            </div>
          </Card>
        </template>

        <template v-slot:ModalActions>
          <Btn hot small @click="onSaveModal">Сохранить</Btn>
          <Btn small @click="onCancelModal">Отмена</Btn>
        </template>

      </ModalDefault>


      <ModalWarningDelete :show.sync="modalWarningDelete.show"
                          @click-yes="onClickYes"
                          @click-not="onClickNo"
      >
      </ModalWarningDelete>

  </div>
</template>

<script>
import Tree from './Tree.vue'
import ModalDefault from './Modal/ModalDefault'
import ModalWarningDelete from './Modal/ModalWarningDelete'
import Btn from './Btn/Btn.vue'
import Card from './Card.vue'

import * as categoryService from '../services/category.service.js'


export default {
  data(){
    return {
      nodes: [],
      nodeEditing: {
      },
      nodeBeforeEditing: {},
      defaultModal: {
        show: false
      },
      modalWarningDelete: {
        show: false
      }
    }
  },
  beforeCreate(){
    categoryService.getMyCategories()
      .then(response => {
        this.nodes = response.data;
      }).catch(err=> console.log(err))
  },
  methods: {
    onEditNode(node){
      this.defaultModal.show = true;
      this.nodeBeforeEditing = Object.assign({}, node)
      this.nodeEditing = node

      console.log('edit node')
    },
    onAddNode(node){
      this.defaultModal.show = true;
      this.nodeBeforeEditing = Object.assign({}, node)
      this.nodeEditing = node
      console.log('add node')
    },
    onDeleteNode(node){
      this.modalWarningDelete.show = true;
      this.nodeEditing = node
      console.log('delete node')
    },

    onMoveNode(node){
        categoryService.updateCategory(node)
        .then(response => {

        })
        .catch(err => console.log(err))
    },
    clearNodeEditing(){
      this.nodeBeforeEditing = {}
      this.nodeEditing = {}
    },

    onClickYes(e){
      this.modalWarningDelete.show = false;
    },
    onClickNo(e){
      this.modalWarningDelete.show = false;
    },

    onSaveModal(e){
      this.defaultModal.show = false;

      if(!this.nodeBeforeEditing.id && Object.keys(this.nodeEditing).length){
        this.nodes.push(this.nodeEditing)
      }
      else{
        this.nodeEditing.nodes.push(this.nodeEditing)
      }

      categoryService.createCategory(this.nodeEditing)
        .then(response => {

        })
        .catch(err => console.log(err))

      this.clearNodeEditing();
    },
    onCancelModal(e){
      this.defaultModal.show = false;
      Object.assign(this.nodeEditing, this.nodeBeforeEditing);
      this.clearNodeEditing();
    },
    onCloseModal(e){
      console.log('ok')
      this.clearNodeEditing();
    }
  },
  components: {
     Tree,
     ModalDefault,
     ModalWarningDelete,
     Btn,
     Card
  }
}

</script>

<style>
  .tree-categories {
    font-size: 14px;
    box-sizing: content-box;
  }

  .category__btn-add {
    background-color: blue;
    color: white;
    width: 100%;
    padding: 10px 0;
    border: none;
    cursor: pointer;
    font-size: 14px;
  }
</style>
