<template>
    <div class="tree">
        <TreeNode v-for="node in nodes"
                  :node="node"
                  :bus="bus"
                  :addTitle="addTitle"
                  :editTitle="editTitle"
                  :deleteTitle="deleteTitle"
                  class="tree__node-root"
        ></TreeNode>
    </div>
</template>

<script>
import Vue from 'vue'
import fastestClone from 'fastest-clone'

import TreeNode from './TreeNode.vue'

export default {
  name: 'Tree',
  props: ['value', 'addTitle', 'editTitle', 'deleteTitle'],
  data(){
    return {
      dragNode: {},
      dropNode: {},
      bus: new Vue()
    }
  },
  computed: {
    nodes: {
      get(){
        return this.value
      },
      set(nodes){
        let length = this.value.length
        for (let i = 0; i < length; i++) {
          this.value.shift(i)
        }

        this.value = Object.assign(this.value, nodes)
      }
    }
  },
  created(){
    this.bus.$on('edit-node', this.onEditNode);
    this.bus.$on('add-node', this.onAddNode);
    this.bus.$on('delete-node', this.onDeleteNode);
    this.bus.$on('toogle-opened', this.onToogleOpened);
    this.bus.$on('toogle-closed', this.onToogleClosed);

    document.onmousemove =  this.onMouseMove;
    document.onmousedown = this.onMouseDown;
    document.onmouseup = this.onMouseUp;

  },
  destroyed(){
    document.removeEventListener('mousemove', this.onMouseMove)
    document.removeEventListener('mousedown', this.onMouseDown)
    document.removeEventListener('mouseup', this.onMouseUp)
  },

  methods: {
    onEditNode(e){
      this.$emit('edit-node', e)
    },
    onAddNode(e){
      this.$emit('add-node', e)
    },
    onDeleteNode(e){
      this.$emit('delete-node', e)
    },
    onToogleOpened(e){
      this.$emit('toogle-opened', e)
    },
    onToogleClosed(e){
      this.$emit('toogle-closed', e)
    },
    // тут спагетти код, переписать под масштабирумеость
    onMouseDown(e){

      if (e.which != 1) { // если клик правой кнопкой мыши
        return; // то он не запускает перенос
      }

      var elem = e.target.closest('.draggable');

      if (!elem) return; // не нашли, клик вне draggable-объекта

      // запомнить переносимый объект
      this.$set(this.dragNode, '$el', elem);

      // запомнить координаты, с которых начат перенос объекта
      this.$set(this.dragNode, 'downX', e.pageX);
      this.$set(this.dragNode, 'downY', e.pageY);

      return false;
    },
    onMouseMove(e){

      if (!this.dragNode.$el) return; // элемент не зажат

      if ( !this.dragNode.avatar ) { // если перенос не начат...

        // посчитать дистанцию, на которую переместился курсор мыши
        var moveX = e.pageX - this.dragNode.downX;
        var moveY = e.pageY - this.dragNode.downY;
        if ( Math.abs(moveX) < 3 && Math.abs(moveY) < 3 ) {
          return; // ничего не делать, мышь не передвинулась достаточно далеко
        }

        this.$set(this.dragNode, 'avatar',  this.createAvatar(e)) // захватить элемент

        if (!this.dragNode.avatar) {
          this.dragNode = {}; // аватар создать не удалось, отмена переноса
          return; // возможно, нельзя захватить за эту часть элемента
        }

        // аватар создан успешно
        // создать вспомогательные свойства shiftX/shiftY
        var coords = this.getCoords(this.dragNode.$el);
        this.$set(this.dragNode, 'shiftX', this.dragNode.downX - coords.left)
        this.$set(this.dragNode, 'shiftY', this.dragNode.downY - coords.top)

        this.startDrag(e); // отобразить начало переноса
      }

      this.scrollPage(e);

      // ищем зону для вставки
      let $newDropNodeEl = this.findDroppable(e);

      if($newDropNodeEl){
        if(!this.dropNode.$el){
          this.dropNode.$el = $newDropNodeEl
          this.onDropEnter({to: this.dropNode, pageX: e.pageX, pageY: e.pageY});
        }
        if(this.dropNode.$el == $newDropNodeEl ){
          this.onDropMove({to: this.dropNode, from: this.dropNode, pageX: e.pageX, pageY: e.pageY});
        }
        if(this.dropNode.$el && this.dropNode.$el != $newDropNodeEl){
          let fromDropNode = Object.assign({}, this.dropNode);
          this.dropNode.$el = $newDropNodeEl;

          this.onDropLeave({to: this.dropNode, from: fromDropNode, pageX: e.pageX, pageY: e.pageY});
          this.onDropEnter({to: this.dropNode, from: fromDropNode, pageX: e.pageX, pageY: e.pageY});
        }
      }else{
        if(this.dropNode.$el){
          this.onDropLeave({from: this.dropNode, pageX: e.pageX, pageY: e.pageY});
          this.dropNode = {}
        }
      }

      // отобразить перенос объекта при каждом движении мыши
      this.dragNode.avatar.style.left = e.pageX + 'px'// - this.dragNode.shiftX + 'px';
      this.dragNode.avatar.style.top = e.pageY  + 'px'// - this.dragNode.shiftY + 'px';

      return false;
    },

    onDropEnter({from, to, pageX, pageY}){
      let dropNode = to;
      let dropNodeItem = {
        '$el': dropNode.$el.querySelector('.tree__node-item')
      };

      let dropNodeItemBound = dropNodeItem.$el.getBoundingClientRect(dropNodeItem.$el);

      dropNodeItem.topPart = {}
      dropNodeItem.topPart.startY = dropNodeItemBound.top + pageYOffset;
      dropNodeItem.topPart.endY = dropNodeItem.topPart.startY + (dropNodeItemBound.height * 0.15);

      dropNodeItem.centerPart = {}
      dropNodeItem.centerPart.startY = dropNodeItem.topPart.endY;
      dropNodeItem.centerPart.endY = dropNodeItem.centerPart.startY + (dropNodeItemBound.height * 0.7);

      dropNodeItem.bottomPart = {}
      dropNodeItem.bottomPart.startY = dropNodeItem.centerPart.endY;
      dropNodeItem.bottomPart.endY = dropNodeItemBound.bottom + pageYOffset;

      this.$set(dropNode, 'item', dropNodeItem);

      this.onDropMove({from, to, pageX, pageY})
    },
    onDropMove({from, to, pageX, pageY}){
      let dropNode = to;
      let draggableObjectY = parseInt(pageY);

      if(draggableObjectY >= dropNode.item.topPart.startY && draggableObjectY < dropNode.item.topPart.endY && !dropNode.item.$el.classList.contains('tree__cursor-before')){
        dropNode.item.$el.classList.remove('tree__cursor-inner');
        dropNode.$el.classList.remove('tree__cursor-after');
        dropNode.item.$el.classList.add('tree__cursor-before');
      }
      if(draggableObjectY >= dropNode.item.centerPart.startY && draggableObjectY < dropNode.item.centerPart.endY && !dropNode.item.$el.classList.contains('tree__cursor-inner')){
        dropNode.item.$el.classList.remove('tree__cursor-before');
        dropNode.$el.classList.remove('tree__cursor-after');
        dropNode.item.$el.classList.add('tree__cursor-inner');
      }
      if(draggableObjectY >= dropNode.item.bottomPart.startY && draggableObjectY <= dropNode.item.bottomPart.endY && !dropNode.$el.classList.contains('tree__cursor-after')){
        dropNode.item.$el.classList.remove('tree__cursor-inner', 'tree__cursor-before');
        dropNode.$el.classList.add('tree__cursor-after');
      }
    },
    onDropLeave({from, to, pageX, pageY}){
      let dropNode = from;
      dropNode.item.$el.classList.remove('tree__cursor-before', 'tree__cursor-inner');
      dropNode.$el.classList.remove('tree__cursor-after');
    },
    scrollPage(e){
      if(e.clientY + 2 >= document.documentElement.clientHeight){
        window.scrollBy(0,  Math.min(document.documentElement.offsetHeight-e.pageY, 10))
      }
      if(e.clientY < 0){
        window.scrollBy(0, -10)
      }
    },
    createAvatar(e) {
      var avatar = this.dragNode.$el.querySelector('.tree__node-item-name').cloneNode(true);
      avatar.style.width = this.dragNode.$el.offsetWidth + 'px';
      avatar.style.fontSize = getComputedStyle(this.dragNode.$el).fontSize;

      return avatar;
    },
    getCoords(elem) { // кроме IE8-
      let box = elem.getBoundingClientRect();

      return {
        top: box.top + pageYOffset,
        left: box.left + pageXOffset
      };
    },
    startDrag(e) {
      let avatar = this.dragNode.avatar;

      document.body.appendChild(avatar);
      this.dragNode.$el.style.opacity = 0.5;
      avatar.style.zIndex = 9999;
      avatar.style.position = 'absolute';
    },
    findDroppable(event) {
       // спрячем переносимый элемент
       this.dragNode.avatar.hidden = true;

       // получить самый вложенный элемент под курсором мыши
       var elem = document.elementFromPoint(event.clientX, event.clientY);

       // показать переносимый элемент обратно
       this.dragNode.avatar.hidden = false;

       if (elem == null || elem.classList.contains('droppable')) {
         // такое возможно, если курсор мыши "вылетел" за границу окна
         return null;
       }

       return elem.closest('.droppable') ;
    },
    finishDrag(e){
      if (!this.dropNode.$el) {
        this.dragCancel();
      } else {
        this.dragEnd();
        this.dropEnd();
      }
    },
    dropEnd(){
      let dropNodeVue = this.dropNode.$el.__vue__;
      let dragNodeVue = this.dragNode.$el.__vue__;
      // данные переносимого элемента
      let copyDragNodeVueNode = Object.assign({}, dragNodeVue.node);

      if(!this.isAccessDrop(dragNodeVue, dropNodeVue)){
        this.dragCancel();
        this.dropNode.item.$el.classList.remove('tree__cursor-before', 'tree__cursor-inner');
        this.dropNode.$el.classList.remove('tree__cursor-after');
        return
      }

      // удаляем данные переносимого элемента
      if(dragNodeVue.$parent.$options._componentTag === 'tree'){
        dragNodeVue.$parent.nodes = dragNodeVue.$parent.nodes.filter(item => {
          return item.id !== copyDragNodeVueNode.id
        })
      }else{
        dragNodeVue.$parent.node.nodes = dragNodeVue.$parent.node.nodes.filter(item => {
          return item.id !== copyDragNodeVueNode.id
        })
      }

      if(!dropNodeVue.node.nodes){
        this.$set(dropNodeVue.node, 'nodes', [])
      }

      if(this.dropNode.item.$el.classList.contains('tree__cursor-before')){
        if(dropNodeVue.$parent.$options._componentTag === 'tree'){
          dropNodeVue.$parent.nodes.splice(dropNodeVue.$parent.nodes.indexOf(dropNodeVue.node), 0, copyDragNodeVueNode)
          copyDragNodeVueNode.parent = null;
          copyDragNodeVueNode.newOrderNo = dropNodeVue.$parent.nodes.indexOf(copyDragNodeVueNode)

        }else{
          dropNodeVue.$parent.node.nodes.splice(dropNodeVue.$parent.node.nodes.indexOf(dropNodeVue.node), 0, copyDragNodeVueNode)
          copyDragNodeVueNode.parent = dropNodeVue.$parent.node.id;
          copyDragNodeVueNode.newOrderNo = dropNodeVue.$parent.node.nodes.indexOf(copyDragNodeVueNode)
        }
        this.$emit('move-node', copyDragNodeVueNode)
      }

      if(this.dropNode.item.$el.classList.contains('tree__cursor-inner')){
        dropNodeVue.node.nodes.push(copyDragNodeVueNode)
        copyDragNodeVueNode.parent = dropNodeVue.node.id,
        copyDragNodeVueNode.newOrderNo = dropNodeVue.node.nodes.indexOf(copyDragNodeVueNode)

        this.$emit('move-node', copyDragNodeVueNode)
      }

      if(this.dropNode.$el.classList.contains('tree__cursor-after')){
        if(dropNodeVue.$parent.$options._componentTag === 'tree'){
          dropNodeVue.$parent.nodes.splice(dropNodeVue.$parent.nodes.indexOf(dropNodeVue.node) + 1, 0, copyDragNodeVueNode)
          copyDragNodeVueNode.parent = null;
          copyDragNodeVueNode.newOrderNo = dropNodeVue.$parent.nodes.indexOf(copyDragNodeVueNode)
        }else{
          dropNodeVue.$parent.node.nodes.splice(dropNodeVue.$parent.node.nodes.indexOf(dropNodeVue.node) + 1, 0, copyDragNodeVueNode)
          copyDragNodeVueNode.parent = dropNodeVue.$parent.node.id;
          copyDragNodeVueNode.newOrderNo = dropNodeVue.$parent.node.nodes.indexOf(copyDragNodeVueNode)
        }

        this.$emit('move-node', copyDragNodeVueNode)
      }

      this.dropNode.item.$el.classList.remove('tree__cursor-before', 'tree__cursor-inner');
      this.dropNode.$el.classList.remove('tree__cursor-after');
    },
    onMouseUp(e){
     // (1) обработать перенос, если он идет
     if (this.dragNode.avatar) {
       this.finishDrag(e);
     }

     // в конце mouseup перенос либо завершился, либо даже не начинался
     // (2) в любом случае очистим "состояние переноса" dragNode
     this.dragNode = {};
     this.dropNode = {};
     return false;
   },
   dragCancel(){
     this.dragNode.$el.style.opacity = 1
     this.dragNode.avatar.remove()
   },
   dragEnd(){
     this.dragNode.$el.style.opacity = 1
     this.dragNode.avatar.remove()
   },
   isAccessDrop(dragNodeVue, dropNodeVue){
     let elem = dropNodeVue;

     while(elem){
       if(elem._uid == dragNodeVue._uid){
         return false;
       }
       elem = elem.$parent;
     }
     return true;
   }
  },

  components: {
     TreeNode
  }
}

</script>

