<template>
  <div class="screen-form">
    <div class="screen-form__inner">
      <form @submit.prevent="signUp()">
          <div class="screen-form__heading">
              <div class="screen-form__heading-logo">
                Make stores
              </div>
              <div class="screen-form__heading-subtitle">
                Добро пожаловать
              </div>
              <div class="screen-form__heading-title">
                <span class="screen-form__heading-title-left">Регистрация в </span>
                <span class="screen-form__heading-title-right"> админ панель</span>
              </div>
          </div>
          <div class="screen-form__section">
            <a href="#" class="screen-form__button ">
              <span class="screen-form__button-icon">
                  <font-awesome-icon :icon="['fab', 'instagram']" size="lg"/>
              </span>
              Через инстаграм
            </a>
          </div>
          <div class="screen-form__separator">
              <span class="screen-form__separator-label">или email</span>
          </div>
          <div class="screen-form__section">
            <div class="screen-form__row" v-for="(field, key) in form.fields">

                <input :type="field.type"
                       :placeholder="field.placeholder"
                       v-model="field.value"
                       autocomplete="off"
                       class="screen-form__input"
                       :class="{'screen-form__input-error': form.errors.has(field.name) }"

                       @change="form.runValidation(field.name)"/>
                <span class="screen-form__input-icon">
                    <font-awesome-icon :icon="field.icon" />
                </span>

                <div class="screen-form__errors" v-if="form.errors.has(field.name)">
                    <div class="screen-form__error">{{form.errors.get(field.name)}}</div>
                </div>
            </div>
          </div>
          <div class="screen-form__submit">
              <button type="submit"  class="screen-form__button">Зарегестрироваться</button>
              <div class="screen-form__errors" v-if="form.errors.has('nonFields')">
                  <div class="screen-form__error">{{form.errors.get('nonFields')}}</div>
              </div>
              <div class="screen-form__errors" v-if="form.errors.has('response')">
                  <div class="screen-form__error">{{form.errors.get('response')}}</div>
              </div>
          </div>
          <div class="screen-form__tip">
              <router-link to="/signin"
                           tag="a"
                           class="screen-form__tip-link"
                           :disabled="form.errors.any()">

                Уже имеете аккаунт?
              </router-link>
          </div>
      </form>
    </div>
  </div>
</template>

<script>

import Vue from 'vue'
import axios from 'axios'
import { AUTH_URL } from '../../env.js'
import { ValidationError, Form } from '../../utils/form.js'



class SignUpForm extends Form{
  constructor(){
    super(
      {
        email: {
            name: 'email',
            type: 'text',
            placeholder: 'Email',
            icon: 'envelope',
            required: true,
            value: ''
        },
        domainName: {
            name: 'domainName',
            type: 'text',
            placeholder: 'Название магазина на латинице',
            icon: 'store',
            required: true,
            value: ''
        },
        password: {
            name: 'password',
            type: 'password',
            placeholder: 'Пароль',
            icon: 'key',
            required: true,
            value: ''
        },
        repeatPassword: {
            name: 'repeatPassword',
            type: 'password',
            placeholder: 'Повторный пароль',
            required: true,
            icon: 'key',
            value: ''
        }
    })
  }

  validateEmail(value){
    return new Promise((resolve, reject) => {
      let pattern = /\S+@\S+\.\S+/;

      if (!pattern.test(value)){
        reject(new ValidationError('Email введен некорректно'));
      }

       axios.post(`${AUTH_URL}/check-email/`, {email: value})
        .then(response => {
          resolve(response)
        })
        .catch(error => {
          if(error.response)
            reject(new ValidationError(error.response.data.email[0]));
          else
            reject(new ValidationError(error.message));
        })

    })
  }

  validateDomainName(value){
    return new Promise((resolve, reject) => {

     axios.post(`${AUTH_URL}/check-domain-name/`, {domainName: value})
      .then(response => {
        resolve(response)
      })
      .catch(error => {
        if(error.response)
          reject(new ValidationError(error.response.data.domainName[0]));
        else
          reject(new ValidationError(error.message));
      })
    })
  }

  validate(data){
    if(data.password.value && data.repeatPassword.value
        && data.password.value != data.repeatPassword.value){
      throw new ValidationError('Пароли не совпадают');
    }

    return data;
  }

}

export default {
  data(){
    return {
      form: new SignUpForm()
    }
  },
  methods: {
      signUp(){
        this.form.runValidation();
        this.form.errors.clear('response');

        if(!this.form.errors.any()){
          this.$store.dispatch('auth/signUp',{
            email: this.form['email'].value,
            password: this.form['password'].value,
            repeatPassword: this.form['repeatPassword'].value,
            domainName: this.form['domainName'].value
          })
            .then(response => {
              this.form.reset()
              this.$router.push({ name: 'orders'})
            })
            .catch(error => {
              if(error.response)
                this.form.errors.record('response',error.response.data)
              else {
                this.form.errors.record('response', error.message)
                throw error
              }
            })
          }

      }
  },
  computed: {
  }
}
</script>

<style src="./screen-form.css" ></style>
