<template>
  <button :class="classes"
          @click="$emit('click', $event)"
          :title="title">
    <slot></slot>
  </button>
</template>

<script>
export default{
  name: 'btn',
  props: {
    hot: {
      type: Boolean,
      default: false
    },
    block: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    small: {
      type: Boolean,
      default: false,
    },
    large: {
      type: Boolean,
      default: false,
    }
  },
  computed: {
    classes(){
      return {
         'btn': true,
         'btn_hot': this.hot,
         'btn_block': this.block,
         'btn_large': this.large,
         'btn_small': this.small
      }
    }
  }
}
</script>

<style>
  .btn{
    color: #000;
    padding: 5px 5px;
    border: none;
    cursor: pointer;
    font-size: 14px;
    box-sizing: border-box;
  }

  .btn_small{
    padding: 5px 8px;
    font-size: 12px;
  }

  .btn_large{
    font-size: 14px;
  }

  .btn_hot{
    background-color: blue;
    color: white;
  }

  .btn_block{
    width: 100%;
  }
</style>
