<template>
  <Modal :show="show"
         :closeOverlay="closeOverlay"
         classes="modal-default"
         @update:show="onShowUpdate"
         @modal-close="$emit('modal-close', $event)">
    <ModalHead>
      <ModalHeadTitle>
        <slot name="ModalHeadTitle"></slot>
      </ModalHeadTitle>
      <ModalClose>
        <slot name="ModalClose"></slot>
      </ModalClose>
    </ModalHead>

    <ModalBody>
      <div class="modal__body-top">
        <slot name="ModalBody"></slot>
      </div>
      <div class="modal__body-bottom">
        <ModalActions>
          <slot name="ModalActions"></slot>
        </ModalActions>
      </div>
    </ModalBody>

  </Modal>
</template>

<script>
import Modal from './Modal.vue'
import ModalHead from './ModalHead.vue'
import ModalHeadTitle from './ModalHeadTitle.vue'
import ModalClose from './ModalClose.vue'
import ModalBody from './ModalBody.vue'
import ModalActions from './ModalActions.vue'

export default{
  name: 'DefaultModal',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    closeOverlay: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    onShowUpdate(e){
      this.$emit('update:show', false);
    }
  },
  watch:{
    show(){
      if(this.show){
        document.documentElement.classList.add('is-clipped')
      }else{
        document.documentElement.classList.remove('is-clipped')
      }
    }
  },
  components:{
    Modal,
    ModalHead,
    ModalHeadTitle,
    ModalClose,
    ModalBody,
    ModalActions
  }
}
</script>

<style>
  @media screen and (max-width: 568px){
    .modal-default .modal__content{
      height: 100vh;
      display: flex;
      flex-direction: column;
      width: 100vw;
      overflow: auto;
    }
    .modal-default .modal__body{
      flex-grow: 1;
    }
    .modal-default .modal__body-content{
      height: 100%;
      display: flex;
      flex-direction: column;
    }
    .modal-default .modal__body-top{
      flex-grow: 1;
      height: 20px;
      overflow-y: auto;
    }

  }
</style>
