import * as authService from '../../services/auth.service.js'

import base64url from 'base64url'

export default {
  namespaced: true,
  state: {
      token: {
        access: localStorage.getItem('access') || '',
        refresh: localStorage.getItem('refresh') || '',
        accessExpiresDate: localStorage.getItem('accessExpiresDate') || ''
      }
  },
  getters: {
    token(state){
      return state.token;
    },
    isAuth(state){
      return !!state.token.access && !!state.token.refresh;
    },
    isAccessTokenExpired (state) {
      const accessTokenExpiresDate = state.token.accessExpiresDate - 1
      const nowTime = Math.floor(new Date().getTime() / 1000)

      return accessTokenExpiresDate <= nowTime

    }
  },
  mutations: {
    setToken(state, response){
      let accessExpiresDate = JSON.parse(base64url.decode(response.data.access.split('.')[1])).exp
      localStorage.setItem('access', response.data.access)
      localStorage.setItem('refresh', response.data.refresh)
      localStorage.setItem('accessExpiresDate', accessExpiresDate)
      state.token.access = response.data.access
      state.token.refresh = response.data.refresh
      state.accessExpiresDate = accessExpiresDate
    },
    removeToken(state, response){
      localStorage.setItem('access', '')
      localStorage.setItem('refresh', '')
      localStorage.setItem('accessExpiresDate', '')
      state.token.access = ''
      state.token.refresh = ''
      state.accessExpiresDate = ''
    }
  },
  actions: {
    signIn({commit, dispatch}, {email, password}){
      return new Promise((resolve, reject) => {
        authService.getTokens({email, password})
          .then(response => {
            commit('setToken', response)
          })
          .then(() => {
            return dispatch('user/getMe', {}, {root: true})
          })
          .then(response => {
            resolve(response)
          })
          .catch(error => {
            commit('removeToken', error)
            reject(error)
          });
      });
    },
    signUp({commit, dispatch}, {email, password, repeatPassword, domainName}){
      return new Promise((resolve, reject) => {
        authService.signUp({email, password, repeatPassword, domainName})
          .then(response => {
            return dispatch('signIn', {email, password})
          })
          .then(response => {
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      });
    },
    refreshTokens({commit, dispatch, getters}){
      return new Promise((resolve, reject) => {
        authService.refreshTokens(getters.token.refresh)
          .then(response => {
            commit('setToken', response)
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    signOut({commit, dispatch}){
      return new Promise((resolve, reject) => {
        commit('removeToken')
        resolve()
      });
    }
  }
}
