<template>
  <Modal :show="show"
         :closeOverlay="closeOverlay"
         @update:show="onShowUpdate"
         @modal-close="$emit('modal-close', $event)">

    <ModalBody>
      <slot name="ModalBody"></slot>
      <ModalActions>
        <slot name="ModalActions"></slot>
      </ModalActions>
    </ModalBody>

  </Modal>
</template>

<script>
import Modal from './Modal.vue'
import ModalBody from './ModalBody.vue'
import ModalActions from './ModalActions.vue'

export default{
  name: 'ModalWarning',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    closeOverlay: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onShowUpdate(e){
      this.$emit('update:show', false);
    }
  },
  components:{
    Modal,
    ModalBody,
    ModalActions
  }
}
</script>
