import WordCase from './word-case.js'
import Vue from 'vue'

export class ValidationError{
    constructor(detail='Error'){
        this.detail = detail
    }
}

class FormError {

  constructor() {
    this.errors = {};
  }

  has(field) {
    return this.errors.hasOwnProperty(field);
  }

  any() {
    return Object.keys(this.errors).length > 0;
  }

  get(field) {
    if (this.errors[field]) {
      return this.errors[field];
    }
  }

  record(field, value) {
    if(Vue){
      Vue.set(this.errors, field, value);
    }else{
      this.errors[field] = value;
    }
  }

  clear(field) {
    if (field) {
      if(Vue){
        Vue.delete(this.errors, field);
      }
      else{
        this.errors[field] = value;
      }

      return;
    }

    this.errors = {};
  }
}

export class Form{

  constructor(data){
    this.fields = data;

    for(let field in this.fields){
      this[field] = this.fields[field];
    }

    this.errors = new FormError();
  }

  data() {
    let data = {};
    let copyField = {};

    for (let field in this.fields) {
      copyField = {}
      for(let property in this.fields[field]){
        copyField[property] = this.fields[field][property];
      }
      data[field] = copyField;
    }

    return data;
  }

  reset() {
    for (let field in this.fields) {
        this[field] = '';
    }

    this.errors.clear();
  }

  validateField(field){
    let validateMethod = this['validate' + WordCase.capitalizeFirstLetter(field)];

    if(this[field].required && !this[field].value){
      this.errors.record(field, 'Данное поле обязательно')
      return
    }

    if(!validateMethod){
      this.errors.clear(field);
    }

    if(validateMethod){
      validateMethod(this[field].value)
        .then(response => {
          this.errors.clear(field);
        })
        .catch( error => {
          if (error instanceof ValidationError){
            this.errors.record(field, error.detail);
          }else{
            throw error;
          }
        })
    }
  }

  validateFields(field){
    if (field){
      this.validateField(field)
    }
    else{
      for(let field in this.fields){
        this.validateField(field)
      }
    }
  }

  runValidation(field){
    this.validateFields(field)

    try{
      // Валидация взаимосвязных полей
      if(this.validate){
        this.validate(this.data())
        this.errors.clear('nonFields');
      }
    } catch (e) {
      if(e instanceof ValidationError){
        this.errors.record('nonFields', e.detail);
      }else {
        throw e;
      }
    }
  }

}
