import axios from 'axios'
import {API_URL} from '../env.js'

import {store} from '../store'


export function getMyCategories(){
  return new Promise((resolve, reject) => {
    axios.get(`${API_URL}/categories/me`)
      .then(response => {
        resolve(response)
      })
      .catch(error => {
        reject(error)
      })
  });
}

export function createCategory(category){
  return new Promise((resolve, reject) => {
    axios.post(`${API_URL}/categories/me/`, category)
      .then(response => {
        resolve(response)
      })
      .catch(error => {
        reject(error)
      })
  });
}

export function updateCategory(category){
  return new Promise((resolve, reject) => {
    let copyCategory = Object.assign({},category,{nodes:[]});
    axios.put(`${API_URL}/categories/me/${category.id}/`, copyCategory)
      .then(response => {
        resolve(response)
      })
      .catch(error => {
        reject(error)
      })
  });
}

export function deleteCategory(category){
  return new Promise((resolve, reject) => {
    axios.delete(`${API_URL}/categories/me/${category.id}/`,{category})
      .then(response => {
        resolve(response)
      })
      .catch(error => {
        reject(error)
      })
  });
}
