import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter);

import {checkAccessMiddleware, setDocumentTitle} from './middlewares'

import SigninPage from '../pages/auth/Signin.vue'
import SignupPage from '../pages/auth/Signup.vue'
import RestorePage from '../pages/auth/Restore.vue'

import OrdersPage from '../pages/default/Orders.vue'
import CategoriesPage from '../pages/default/products/Categories.vue'
import ProductsListPage from '../pages/default/products/ProductsList.vue'
import PropertiesPage from '../pages/default/products/Properties.vue'

import DeliveryPage from '../pages/default/settings/Delivery.vue'
import GeneralPage from '../pages/default/settings/General.vue'
import OrderRegPage from '../pages/default/settings/OrderReg.vue'
import PaymentsPage from '../pages/default/settings/Payments.vue'

import Error404Page from '../pages/Error404.vue'

let startPath = '/a';

export const router = new VueRouter({
  mode: 'history',
  routes: [

    {
      path: `/signin`,
      name:'signIn',
      component: SigninPage,
      meta: {
        layout: 'AuthLayout'
      }
    },
    {
      path: `/signup`,
      name:'signUp',
      component: SignupPage,
      meta: {
        layout: 'AuthLayout'
      }
    },
    {
      path: `/restore`,
      component: RestorePage,
      meta: {
        layout: 'AuthLayout'
      }
    },


    {
      path: `${startPath}/orders`,
      name: 'orders',
      component: OrdersPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Заказы',
          subtitle: ''
        }
      }
    },

    {
      path: `${startPath}/products/cat`,
      component: CategoriesPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Категории',
          subtitle: ''
        }
      }
    },
    {
      path: `${startPath}/products/products-list`,
      component: ProductsListPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Список товаров',
          subtitle: ''
        }
      }
    },
    {
      path: `${startPath}/products/properties`,
      component: PropertiesPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Свойства',
          subtitle: ''
        }
      }
    },

    {
      path: `${startPath}/settings/order-reg`,
      component: OrderRegPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Оформление заказа',
          subtitle: ''
        }
      }
    },
    {
      path: `${startPath}/settings/delivery`,
      component: DeliveryPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Доставка',
          subtitle: ''
        }
      }
    },
    {
      path: `${startPath}/settings/payments`,
      component: PaymentsPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Платежи',
          subtitle: ''
        }
      }
    },
    {
      path: `${startPath}/settings/general`,
      component: PaymentsPage,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Общие',
          subtitle: ''
        }
      }
    },

    {
      path: `${startPath}/products`,
      redirect: `${startPath}/products/cat`,
    },
    {
      path: `${startPath}/settings`,
      redirect: `${startPath}/settings/order-reg`,
    },

    {
      path: '*',
      component: Error404Page,
      meta: {
        isAuth: true,
        layout: 'DefaultLayout',
        page: {
          title: 'Страница не найдена',
          subtitle: ''
        }
      }
    }
  ]

})

router.beforeEach(checkAccessMiddleware)
router.beforeEach(setDocumentTitle)
